.container {
    position: relative;
}

.video-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.background-video {
    width: 100%;
    height: auto;
    object-fit: cover;
    z-index: -1;
    opacity: 90%;
}

.image_grid_onoff {
    position: relative;
    z-index: 1;

    display: grid;
    grid-template-columns: repeat(2, 450px);
    justify-content: center; /* 가로 중앙 정렬 */
    align-items: center; /* 세로 중앙 정렬 */
    height: 100vh; /* 화면 높이에 따라 중앙에 배열 */
}