/* Firstpage */

.FirstPage_body{
    width: 100vw;
    height: 100vh;
    background-color: #007BBE;
    background-size: cover;
    overflow: hidden;
}

.logo{
    width: 280px;
    height: 175px;
    flex-shrink: 0;
    position: relative;
    padding: 270px 175px;
}

.LoginButton{
    width: 161px;
    height: 73px;
    position: absolute;
    top: 450px;
    left: 180px;
    transition: transform 0.3s ease;
}

.LoginButton:hover {
    transform: scale(1.1);
}

.SignUpButton{
    width: 161px;
    height: 73px;
    flex-shrink: 0;
    position: absolute;
    top: 450px;
    left : 385px;
    transition: transform 0.3s ease;
}

.SignUpButton:hover{
    transform: scale(1.1);
}


.video {
    position: relative;
    bottom: 500px;
    left: 900px;
    width: 30%;
    height: 30%;
    margin: 0 auto;
    object-fit: cover;
    z-index: 2;
}